import { defineStore } from "pinia";
import axios from "axios";
import Swal from "sweetalert2";

export const useCartStore = defineStore('cart',{
  state: () => ({
    carts: [],
  }),
  getters: {
    getNum(state) {
      return state.carts.length;
    },
  },
  actions:{
    getItem() {
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
      axios.get(url)
      .then((res) => {
        this.carts = res.data.data.carts;
      })
    },
    addItem(item) {
      let isAdd = 0;
      this.carts.forEach(function (i) {
        if (i.product_id == item.id) isAdd = 1;
      });
      if (!isAdd) {
        const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
        const cart = {
          product_id: item.id,
          qty: 1,
        };
        axios.post(url, { data: cart })
        .then((res) => {
          this.getItem();
          Swal.fire({
            icon: 'success',
            text: '',
            iconColor: '#ffd93d',
            confirmButtonText:'',
            showConfirmButton: false,
            confirmButtonColor: '#ffd93d',
            timer: 1200,
          })
        })
      } else {
        Swal.fire({
          icon: 'info',
          text: '',
          iconColor: '#ff8400',
          confirmButtonText:'',
          confirmButtonColor: '#ffd93d',
        })
        return;
      }
    },
  },
  
});